import React from 'react';
import { Paper, Typography, Box, Avatar, List, ListItem, ListItemIcon, ListItemText } from '@mui/material';
import EmailIcon from '@mui/icons-material/Email';
import GitHubIcon from '@mui/icons-material/GitHub';
import profilePic from '../img/profile.png';

const Profile = () => {
  return (
    <Paper elevation={1} sx={{ padding: 3, maxWidth: 345 }}>
      <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
        <Avatar 
          variant='rounded'
          alt="Levi Blankenheim"
          src={profilePic}
          sx={{ width: 128, height: 128, marginBottom: 2 }}
        />
        <Typography variant="h6">Levi Blankenheim</Typography>
        <Typography variant="body2" color="textSecondary">Backend Developer</Typography>
      </Box>
      <List>
        <ListItem>
          <ListItemIcon>
            <EmailIcon color="tertiary"/>
          </ListItemIcon>
          <a style={{color: "darkgreen", textDecoration: 'none'}} href="mailto:blankenheimlevi@gmail.com"><ListItemText primary="blankenheimlevi@gmail.com" /></a>
        </ListItem>
        <ListItem>
          <ListItemIcon>
            <GitHubIcon color="tertiary"/>
          </ListItemIcon>
          <a style={{color: "darkgreen", textDecoration: 'none'}} rel="noreferrer" target="_blank" href="https://github.com/Levy8Me"><ListItemText primary="Levy8Me" /></a>
        </ListItem>
      </List>
    </Paper>
  );
};

export default Profile;
