import React from 'react';
import Resume from './Resume';
import AboutMe from './AboutMe';
import Projects from './Projects';

const ContentArea = ({ selectedSection }) => {
  switch (selectedSection) {
    case '/me':
      return <AboutMe />;
    case '/resume':
      return <Resume />;
    case '/projects':
      return <Projects />;
    default:
      return <AboutMe />;
  }
};

export default ContentArea;