import { Container, Box } from '@mui/material';
import React from 'react';

const Footer = () => {
    return (
        <Box component='footer'
            sx={{
                textAlign: 'center',
                position: 'fixed',
                left: 0,
                bottom: 0,
                height: '50px',
                width: '100%',
                bgcolor: 'background.paper',
                padding: '16px 0',
                boxShadow: '0 -2px 10px rgba(0,0,0,0.12)',
            }}>
            <Container>
                &copy; 2023 Levi Blankenheim - <a style={{color: "darkgreen", textDecoration: 'none'}} rel="noreferrer" target="_blank" href="https://github.com/Levy8Me/levy8me.github.io">Source Code</a>
            </Container>
        </Box>
    );
}

export default Footer;