import React from "react";
import { Box, Grid, Typography } from "@mui/material";
import Experience from './Experience';
import Education from './Education';
import Skills from './Skills';
import Profile from "./Profile";

const skills = [
  {
    title: "Web Development",
    names: ['JavaScript', 'React', 'HTML', 'CSS'],
  },
  {
    title: "Backend Development",
    names: ['C#', 'DevOps', 'Gherkin'],
  },
  {
    title: "API Development",
    names: ['Postman', 'Azure'],
  },
];

const experience = [
  {
    title: 'Software Developer',
    company: 'ABC Corp',
    time: 'Jan 2020 - Present',
    description: 'Developing and maintaining web applications using React and Node.js.',
  },
];

const education = [
  {
    degree: 'BS - Computer Science (Minor in Psychology)',
    institution: 'University of Wisconsin - Whitewater',
    time: '2020 - 2024'
  },
];

const Resume = () => {
  return (
    <Box>
      <Grid flex>
        <Typography margin={1} variant="h5" align="center" fontWeight={700}>Resume</Typography>
      </Grid>
      <Grid container spacing={2}>
        <Grid item xs={12} sm={4} display="container" justifyContent="center">
          <Grid><Profile /></Grid>
        </Grid>
        <Grid item xs={12} sm={8}>
          <Experience experience={experience} />
          <Education education={education} />
          <Skills skills={skills} />
        </Grid>
      </Grid>
    </Box>
  );
};

export default Resume;