import React from "react";
import { Box, Typography, Paper, Grid } from "@mui/material";
import Profile from "./Profile";

const AboutMe = () => {
    return (
        <Box>
            <Grid flex>
                <Typography margin={1} variant="h5" align="center" fontWeight={700}>About Me</Typography>
            </Grid>
            <Grid container spacing={2}>
                <Grid item xs={12} sm={4} display="container" justifyContent="center">
                    <Grid><Profile /></Grid>
                </Grid>
                <Grid item xs={12} sm={8}>
                    <Paper elevation={3} sx={{ marginBottom: 1, p: 2 }}>
                        <Typography variant="h5" gutterBottom>Myself</Typography>
                        <Typography variant="subtitle1">I grew up in...(More to come)</Typography>
                    </Paper>
                </Grid>
            </Grid>
        </Box>
    );
};

export default AboutMe;