import React from 'react';
import { Paper, Typography, List, Divider} from '@mui/material';

const Skills = ({ skills, mBottom }) => {
    return (
            <Paper elevation={3} sx={{ marginBottom: 1, p: 2 }}>
                <Typography variant="h5" gutterBottom>
                    Skills
                </Typography>
                <List>
                    {skills.map((skillCategory, index) => (
                        <React.Fragment key={index}>
                            <Typography variant="h6">{skillCategory.title}</Typography>
                            {skillCategory.names.map((name, nameIndex) => (
                                <Typography key={nameIndex} variant="subtitle1">&bull; {name}</Typography>
                            ))}
                            {index < skills.length - 1 && <Divider />}
                        </React.Fragment>
                    ))}
                </List>
            </Paper>
    );
};

export default Skills;