import React from 'react';
import { Button, Toolbar, AppBar, Typography } from '@mui/material';

const NavigationBar = ({ buttons, onSectionChange }) => {
    return (
        <nav>
            <AppBar position="static" color="primary" sx={{ marginBottom: '0.5rem', marginTop: '0.5rem' }}>
                <Toolbar variant='regular'>
                    <Typography variant="h6" component="div" sx={{ flexGrow: 1, color: 'text.tertiary' }}>Levi</Typography>
                    {buttons.map((button, index) => (
                        <React.Fragment key={index}>
                            <Button key={button.path} sx={{ color: 'text.tertiary', fontWeight: 700 }} onClick={() => onSectionChange(button.path)}>{button.label}</Button>
                            {index < buttons.length - 1}
                        </React.Fragment>
                    ))}
                </Toolbar>
            </AppBar>
        </nav>
    );
}

export default NavigationBar;